import { v4 as uuidv4 } from 'uuid';

class TestCaseGenerator {
  
  // Ana test case retimi fonksiyonu
  static async generateFromDescription(imageFile, description) {
    try {
      // Aklamay analiz et
      const analysis = this.analyzeDescription(description);
      
      // UI componentlerini tahmin et (gerek projede image analysis kullanlabilir)
      const uiComponents = this.detectUIComponents(analysis);
      
      // Test case'leri ret
      const testCases = this.generateTestCases(uiComponents, analysis);
      
      return testCases;
      
    } catch (error) {
      console.error('Test case generation failed:', error);
      throw error;
    }
  }

  // Aklamay analiz ederek anahtar kelimeleri kar
  static analyzeDescription(description) {
    const lowerDesc = description.toLowerCase();
    
    const analysis = {
      components: [],
      testTypes: [],
      priority: 'Medium',
      userType: 'user',
      requirements: []
    };

    // UI Componentlerini tespit et
    const componentKeywords = {
      'dropdown': ['dropdown', 'alr', 'seim', 'liste', 'men'],
      'button': ['buton', 'button', 'tkla', 'bas'],
      'form': ['form', 'giri', 'kayt', 'doldur'],
      'input': ['input', 'alan', 'field', 'giri'],
      'navigation': ['men', 'nav', 'navigasyon', 'ynlendirme'],
      'table': ['tablo', 'table', 'liste', 'satr'],
      'modal': ['modal', 'popup', 'pencere'],
      'search': ['arama', 'search', 'filtre'],
      'upload': ['ykle', 'upload', 'dosya'],
      'login': ['giri', 'login', 'oturum']
    };

    for (const [component, keywords] of Object.entries(componentKeywords)) {
      if (keywords.some(keyword => lowerDesc.includes(keyword))) {
        analysis.components.push(component);
      }
    }

    // Test tiplerini tespit et
    const testTypeKeywords = {
      'functional': ['ilevsel', 'functional', 'al'],
      'ui': ['grnm', 'ui', 'arayz', 'grsel'],
      'validation': ['dorula', 'validation', 'kontrol', 'geerli'],
      'accessibility': ['eriilebilir', 'accessibility', 'keyboard'],
      'performance': ['performans', 'hz', 'yava'],
      'negative': ['negative', 'hatal', 'yanl', 'geersiz']
    };

    for (const [testType, keywords] of Object.entries(testTypeKeywords)) {
      if (keywords.some(keyword => lowerDesc.includes(keyword))) {
        analysis.testTypes.push(testType);
      }
    }

    // Varsaylan test tipleri
    if (analysis.testTypes.length === 0) {
      analysis.testTypes = ['functional', 'ui'];
    }

    // ncelik belirleme
    if (lowerDesc.includes('kritik') || lowerDesc.includes('nemli') || lowerDesc.includes('high')) {
      analysis.priority = 'High';
    } else if (lowerDesc.includes('dk') || lowerDesc.includes('low')) {
      analysis.priority = 'Low';
    }

    // Kullanc tipi
    if (lowerDesc.includes('admin') || lowerDesc.includes('ynetici')) {
      analysis.userType = 'admin';
    }

    return analysis;
  }

  // UI componentlerini tespit et
  static detectUIComponents(analysis) {
    const components = analysis.components.length > 0 ? 
      analysis.components : 
      ['button', 'form']; // varsaylan componentler

    return components.map(component => ({
      type: component,
      name: this.getComponentName(component),
      testScenarios: this.getComponentScenarios(component)
    }));
  }

  // Component isimleri
  static getComponentName(componentType) {
    const names = {
      'dropdown': 'Dropdown Men',
      'button': 'Buton',
      'form': 'Form',
      'input': 'Giri Alan',
      'navigation': 'Navigasyon',
      'table': 'Tablo',
      'modal': 'Modal Pencere',
      'search': 'Arama',
      'upload': 'Dosya Ykleme',
      'login': 'Giri Sistemi'
    };
    return names[componentType] || componentType;
  }

  // Component iin test senaryolar
  static getComponentScenarios(componentType) {
    const scenarios = {
      'dropdown': [
        'Tm seeneklerin grntlenmesi',
        'Seim yapma ilemi',
        'Keyboard navigation',
        'Varsaylan deer kontrol',
        'Bo seim validation'
      ],
      'button': [
        'Buton tklama ilevi',
        'Hover state kontrol',
        'Disabled state kontrol',
        'Loading state kontrol'
      ],
      'form': [
        'Form doldurma ilemi',
        'Validation kurallar',
        'Submit ilemi',
        'Reset ilemi',
        'Error handling'
      ],
      'input': [
        'Veri girii',
        'Validation kontrolleri',
        'Placeholder text',
        'Character limit',
        'Format kontrol'
      ],
      'navigation': [
        'Men elerine tklama',
        'Sayfa ynlendirmeleri',
        'Breadcrumb kontrol',
        'Mobile responsive'
      ],
      'table': [
        'Veri grntleme',
        'Sralama ilemleri',
        'Filtreleme',
        'Pagination',
        'Row selection'
      ],
      'modal': [
        'Modal almas',
        'Modal kapanmas',
        'Overlay tklama',
        'ESC tuu ile kapanma'
      ],
      'search': [
        'Arama ilevi',
        'Sonu grntleme',
        'Bo arama',
        'Filtre uygulama'
      ],
      'upload': [
        'Dosya seimi',
        'Drag & drop',
        'Dosya validation',
        'Upload progress'
      ],
      'login': [
        'Baarl giri',
        'Hatal giri',
        'ifre unuttum',
        'Session ynetimi'
      ]
    };

    return scenarios[componentType] || ['Temel ilevsellik kontrol'];
  }

  // Test case'leri ret
  static generateTestCases(uiComponents, analysis) {
    const testCases = [];
    let testIdCounter = 1;

    uiComponents.forEach(component => {
      component.testScenarios.forEach(scenario => {
        analysis.testTypes.forEach(testType => {
          const testCase = this.createTestCase(
            testIdCounter++,
            component,
            scenario,
            testType,
            analysis
          );
          testCases.push(testCase);
        });
      });
    });

    // Edge case'ler ve negative testler ekle
    testCases.push(...this.generateEdgeCaseTests(uiComponents, analysis, testIdCounter));

    return testCases;
  }

  // Tek bir test case olutur
  static createTestCase(id, component, scenario, testType, analysis) {
    const priority = this.determinePriority(scenario, testType, analysis);
    const steps = this.generateTestSteps(component, scenario, testType);
    
    return {
      id: `TC_${String(id).padStart(3, '0')}`,
      uuid: uuidv4(),
      summary: `${component.name} - ${scenario}`,
      description: `${component.name} bileenindeki ${scenario.toLowerCase()} ilevselliinin ${testType} testi`,
      testType: testType === 'functional' ? 'Manual' : 'Manual',
      priority: priority,
      component: component.name,
      labels: [testType, component.type, this.getTestLabel(scenario)],
      preconditions: this.generatePreconditions(component, analysis),
      steps: steps,
      expectedResults: this.generateExpectedResults(component, scenario),
      testData: this.generateTestData(component, scenario),
      estimatedTime: this.estimateTestTime(steps.length),
      createdAt: new Date().toISOString()
    };
  }

  // Test admlarn ret
  static generateTestSteps(component, scenario, testType) {
    const baseSteps = [
      'Uygulamaya giri yap',
      `${component.name} bileenine git`
    ];

    let specificSteps = [];

    if (component.type === 'dropdown') {
      specificSteps = [
        'Dropdown mensne tkla',
        'Tm seeneklerin grntlendiini kontrol et',
        'Bir seenek se',
        'Seimin yapldn dorula'
      ];
    } else if (component.type === 'form') {
      specificSteps = [
        'Form alanlarn doldur',
        'Gerekli alanlar kontrol et',
        'Submit butonuna tkla',
        'lem sonucunu kontrol et'
      ];
    } else if (component.type === 'button') {
      specificSteps = [
        'Butonun grnr olduunu kontrol et',
        'Butona tkla',
        'Beklenen aksiyonun gerekletiini dorula'
      ];
    } else {
      specificSteps = [
        `${component.name} ile etkileim kur`,
        'Beklenen davran kontrol et'
      ];
    }

    return [...baseSteps, ...specificSteps];
  }

  // Beklenen sonular ret
  static generateExpectedResults(component, scenario) {
    const baseResults = [
      'Baaryla giri yaplr',
      `${component.name} bileeni eriilebilir durumda`
    ];

    if (component.type === 'dropdown') {
      baseResults.push(
        'Dropdown men alr',
        'Tm seenekler listelenir',
        'Seim ilemi baaryla tamamlanr'
      );
    } else if (component.type === 'form') {
      baseResults.push(
        'Form alanlar doldurulur',
        'Validation kurallar uygulanr',
        'Form baaryla submit edilir'
      );
    } else {
      baseResults.push(
        'Bileen beklenen ekilde alr',
        'lem baaryla tamamlanr'
      );
    }

    return baseResults;
  }

  // Test verilerini ret
  static generateTestData(component, scenario) {
    const testData = {
      validUser: 'test_user@example.com',
      environment: 'Test Environment'
    };

    if (component.type === 'dropdown') {
      testData.options = ['Seenek 1', 'Seenek 2', 'Seenek 3'];
    } else if (component.type === 'form') {
      testData.validInputs = {
        name: 'Test User',
        email: 'test@example.com',
        phone: '5551234567'
      };
      testData.invalidInputs = {
        email: 'invalid-email',
        phone: '123'
      };
    }

    return testData;
  }

  // Edge case testleri ret
  static generateEdgeCaseTests(uiComponents, analysis, startId) {
    const edgeCases = [];
    
    // Performance test
    edgeCases.push({
      id: `TC_${String(startId).padStart(3, '0')}`,
      uuid: uuidv4(),
      summary: 'Performans Testi - Sayfa Ykleme',
      description: 'Sayfann makul srede yklendiinin kontrol',
      testType: 'Manual',
      priority: 'Low',
      component: 'Page',
      labels: ['performance', 'loading'],
      preconditions: ['Browser ak', 'Internet balants mevcut'],
      steps: [
        'Sayfay yenile',
        'Ykleme sresini l',
        'Tm elementlerin yklendiini kontrol et'
      ],
      expectedResults: [
        'Sayfa 3 saniye iinde yklenir',
        'Tm elementler grnr durumda'
      ],
      testData: { maxLoadTime: '3 seconds' },
      estimatedTime: '5 minutes',
      createdAt: new Date().toISOString()
    });

    // Browser compatibility test
    edgeCases.push({
      id: `TC_${String(startId + 1).padStart(3, '0')}`,
      uuid: uuidv4(),
      summary: 'Cross-Browser Compatibility',
      description: 'Farkl tarayclarda uyumluluun kontrol',
      testType: 'Manual',
      priority: 'Medium',
      component: 'Application',
      labels: ['compatibility', 'browser'],
      preconditions: ['Farkl tarayclar ykl'],
      steps: [
        'Chrome\'da uygulamay a',
        'Firefox\'ta uygulamay a',
        'Safari\'de uygulamay a (Mac)',
        'Her taraycda ilevsellii test et'
      ],
      expectedResults: [
        'Tm tarayclarda ayn grnm',
        'levsellik farkll yok'
      ],
      testData: { browsers: ['Chrome', 'Firefox', 'Safari', 'Edge'] },
      estimatedTime: '15 minutes',
      createdAt: new Date().toISOString()
    });

    return edgeCases;
  }

  // Yardmc fonksiyonlar
  static determinePriority(scenario, testType, analysis) {
    if (testType === 'functional' || scenario.includes('Ana')) {
      return 'High';
    } else if (testType === 'validation' || testType === 'ui') {
      return 'Medium';
    }
    return 'Low';
  }

  static getTestLabel(scenario) {
    if (scenario.includes('Keyboard')) return 'accessibility';
    if (scenario.includes('Validation')) return 'validation';
    if (scenario.includes('Error')) return 'negative';
    return 'functional';
  }

  static generatePreconditions(component, analysis) {
    const basePreconditions = [
      'Test ortamnda uygulama eriilebilir',
      'Geerli kullanc hesab mevcut'
    ];

    if (analysis.userType === 'admin') {
      basePreconditions.push('Admin yetkilerine sahip kullanc');
    }

    return basePreconditions;
  }

  static estimateTestTime(stepCount) {
    const baseTime = 3; // dakika
    const perStepTime = 1; // dakika per step
    return `${baseTime + (stepCount * perStepTime)} dakika`;
  }
}

export default TestCaseGenerator; 