import React, { useState } from 'react';
import { MessageSquare, Lightbulb, Target, Layers } from 'lucide-react';

const DescriptionInput = ({ description, onChange }) => {
  const [activeTab, setActiveTab] = useState('description');
  
  const examplePrompts = [
    {
      title: "Dropdown Test",
      description: "Bu ekranda bir dropdown men var. Tm seeneklerin doru grntlendiini ve seim ilemlerinin altn test etmek istiyorum.",
      icon: ""
    },
    {
      title: "Form Validation",
      description: "Login formundaki validation kurallarn test etmek istiyorum. Bo field'lar, geersiz email format ve ifre kriterleri kontrol edilmeli.",
      icon: ""
    },
    {
      title: "Navigation Test",
      description: "Ana men navigasyonunu test etmek istiyorum. Tm men elerinin doru sayfalara ynlendirdiini kontrol edilmeli.",
      icon: ""
    },
    {
      title: "Button Actions",
      description: "Sayfadaki tm butonlarn ilevselliini test etmek istiyorum. Kaydet, iptal, dzenle gibi aksiyonlar kontrol edilmeli.",
      icon: ""
    }
  ];

  const handleExampleClick = (exampleDescription) => {
    onChange(exampleDescription);
  };

  const tabs = [
    { id: 'description', label: 'Aklama', icon: MessageSquare },
    { id: 'examples', label: 'rnekler', icon: Lightbulb }
  ];

  return (
    <div className="space-y-4">
      {/* Tab Navigation */}
      <div className="flex space-x-4 border-b border-gray-200">
        {tabs.map(({ id, label, icon: Icon }) => (
          <button
            key={id}
            onClick={() => setActiveTab(id)}
            className={`
              flex items-center space-x-2 px-4 py-2 border-b-2 transition-colors duration-200
              ${activeTab === id 
                ? 'border-xray-blue text-xray-blue' 
                : 'border-transparent text-gray-500 hover:text-gray-700'
              }
            `}
          >
            <Icon className="h-4 w-4" />
            <span className="font-medium">{label}</span>
          </button>
        ))}
      </div>

      {/* Description Tab */}
      {activeTab === 'description' && (
        <div className="space-y-4">
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700 flex items-center">
              <Target className="h-4 w-4 mr-1" />
              Test edilecek zellikler ve beklentilerinizi aklayn
            </label>
            <textarea
              value={description}
              onChange={(e) => onChange(e.target.value)}
              placeholder="rnek: Bu ekranda bulunan dropdown mennn tm seeneklerini test etmek istiyorum. Seeneklerin doru grntlendii, seim ilemlerinin alt ve validation kurallarnn uygulandn kontrol edilmeli..."
              className="form-input min-h-[120px] resize-y"
              rows={6}
            />
            <div className="text-right text-sm text-gray-500">
              {description.length}/1000 karakter
            </div>
          </div>

          {/* Tips for good descriptions */}
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <h4 className="font-medium text-blue-900 mb-2 flex items-center">
              <Lightbulb className="h-4 w-4 mr-1" />
              yi Aklama pular
            </h4>
            <ul className="text-sm text-blue-800 space-y-1">
              <li> <strong>Spesifik olun:</strong> Hangi UI elementlerini test etmek istediinizi belirtin</li>
              <li> <strong>Senaryolar aklayn:</strong> Normal kullanm ve edge case'leri tanmlayn</li>
              <li> <strong>Beklentileri belirtin:</strong> Ne olmas gerektiini aka ifade edin</li>
              <li> <strong>Kullanc tipini ekleyin:</strong> Admin, normal kullanc vs.</li>
            </ul>
          </div>

          {/* Quick suggestion buttons */}
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700">
              Hzl Eklemeler:
            </label>
            <div className="flex flex-wrap gap-2">
              {[
                "Functional testing gerekli",
                "UI validation kontrol",
                "Negative test case'ler dahil",
                "Accessibility testleri",
                "Performance testleri",
                "Cross-browser compatibility"
              ].map((suggestion) => (
                <button
                  key={suggestion}
                  onClick={() => {
                    const newDescription = description ? 
                      `${description}\n ${suggestion}` : 
                      suggestion;
                    onChange(newDescription);
                  }}
                  className="px-3 py-1 bg-gray-100 hover:bg-gray-200 text-gray-700 text-sm rounded-full transition-colors"
                >
                  + {suggestion}
                </button>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Examples Tab */}
      {activeTab === 'examples' && (
        <div className="space-y-4">
          <p className="text-sm text-gray-600">
            Aadaki rnek aklamalar kullanarak hzlca balayabilirsiniz:
          </p>
          
          <div className="grid gap-4">
            {examplePrompts.map((example, index) => (
              <div
                key={index}
                className="border border-gray-200 rounded-lg p-4 hover:border-xray-blue hover:bg-xray-light/20 transition-all duration-200 cursor-pointer"
                onClick={() => handleExampleClick(example.description)}
              >
                <div className="flex items-start space-x-3">
                  <span className="text-2xl">{example.icon}</span>
                  <div className="flex-1">
                    <h4 className="font-medium text-gray-900 mb-1">
                      {example.title}
                    </h4>
                    <p className="text-sm text-gray-600 leading-relaxed">
                      {example.description}
                    </p>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
            <h4 className="font-medium text-yellow-900 mb-2">
               zelletirme nerisi
            </h4>
            <p className="text-sm text-yellow-800">
              Bu rnekleri kendi ekran grntnze gre uyarlayn. 
              Ne kadar detayl aklama verirseniz, o kadar hedefli test case'ler oluur.
            </p>
          </div>
        </div>
      )}

      {/* Character count and validation */}
      {description.length > 800 && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-3">
          <p className="text-sm text-green-800 flex items-center">
            <Layers className="h-4 w-4 mr-1" />
            Harika! Detayl aklama daha kaliteli test case'ler oluturacak.
          </p>
        </div>
      )}
    </div>
  );
};

export default DescriptionInput; 